
'use client'; // Add 'use client' since we might need client-side logic (like checking auth)

import type { Metadata } from 'next';
import { Geist, Geist_Mono } from 'next/font/google';
import Link from 'next/link';
import { usePathname, useRouter } from 'next/navigation'; // Import usePathname and useRouter
import { useEffect, useState } from 'react'; // Import hooks
import './globals.css';
import { Toaster } from '@/components/ui/toaster';
import { Button } from '@/components/ui/button';
import { Home, History, MapPin, LogOut, Building, UserCog, Users } from 'lucide-react'; // Added LogOut, Building, UserCog, Users
import type { LoggedInUser } from '@/lib/types'; // Import LoggedInUser type
import { cn } from '@/lib/utils'; // Import cn

const geistSans = Geist({
  variable: '--font-geist-sans',
  subsets: ['latin'],
});

const geistMono = Geist_Mono({
  variable: '--font-geist-mono',
  subsets: ['latin'],
});

// Metadata can still be defined for server rendering, but might be less relevant if layout needs client logic
/* export const metadata: Metadata = {
  title: 'FieldTrack - E Wheels and Logistics',
  description: 'Attendance tracking for field staff with selfie and location.',
}; */

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  const pathname = usePathname(); // Get the current path
  const router = useRouter();
  const [loggedInUser, setLoggedInUser] = useState<LoggedInUser | null>(null);
  const [isAdmin, setIsAdmin] = useState(false);

  useEffect(() => {
    // Check localStorage only on the client-side
    if (typeof window !== 'undefined') {
      const userString = localStorage.getItem('loggedInUser');
      if (userString) {
          try {
              const user: LoggedInUser = JSON.parse(userString);
              setLoggedInUser(user);
              setIsAdmin(user.type === 'admin'); // Check if the logged-in user is admin
          } catch (e) {
              console.error("Failed to parse loggedInUser from localStorage", e);
              localStorage.removeItem('loggedInUser'); // Clear invalid data
              setLoggedInUser(null);
              setIsAdmin(false);
          }
      } else {
          setLoggedInUser(null);
          setIsAdmin(false);
      }
    }
  }, [pathname]); // Re-check on path change

  const handleLogout = () => {
    localStorage.removeItem('loggedInUser');
    setLoggedInUser(null);
    setIsAdmin(false);
    // Redirect to login page after logout
    router.push('/login');
  };

  // Determine if the header should be shown (e.g., hide on login page)
  const showHeader = pathname !== '/login';
  const isLoginPage = pathname === '/login';

  return (
    <html lang="en">
      {/* No whitespace directly inside head */}
      <head>{/* PWA Meta Tags */}
        <meta name="application-name" content="FieldTrack" />
        <meta name="apple-mobile-web-app-capable" content="yes" />
        <meta name="apple-mobile-web-app-status-bar-style" content="default" />
        <meta name="apple-mobile-web-app-title" content="FieldTrack" />
        <meta name="format-detection" content="telephone=no" />
        <meta name="mobile-web-app-capable" content="yes" />
        {/* Consider adding more specific PWA tags like msapplication-config if needed */}
        <meta name="theme-color" content="#2C3E50" /> {/* Match primary color */}
        {/* Link to Manifest */}
        <link rel="manifest" href="/manifest.json" />
        {/* Placeholder Icons (Replace with actual icons in public/ folder) */}
        <link rel="apple-touch-icon" sizes="180x180" href="/icon-192x192.png" />
        <link rel="icon" type="image/png" sizes="32x32" href="/icon-192x192.png" />
        <link rel="icon" type="image/png" sizes="16x16" href="/icon-192x192.png" />
        {/* Set title dynamically or keep static */}
        <title>FieldTrack - E Wheels and Logistics</title>
        <meta name="description" content="Attendance tracking for field staff with selfie and location." />
      </head>
      <body
        className={cn(
            `${geistSans.variable} ${geistMono.variable} antialiased flex flex-col min-h-screen`,
            // Apply default background unless it's the login page
            isLoginPage ? '' : 'bg-secondary'
         )}
      >
       {showHeader && (
        // Header uses primary background and primary-foreground text
        <header className="sticky top-0 z-50 w-full border-b bg-primary text-primary-foreground shadow-sm">
          <div className="container flex h-16 items-center justify-between px-4 md:px-6">
            <Link href="/" className="flex items-center gap-2">
              {/* Logo icon uses accent color */}
              <MapPin className="h-6 w-6 text-accent" />
              <div className="flex flex-col">
                 {/* Title uses primary-foreground color (inherited) */}
                 <span className="text-xl font-semibold">FieldTrack</span>
                 {/* Subtitle uses a slightly muted version of primary-foreground */}
                 <span className="text-xs text-primary-foreground/80 flex items-center gap-1">
                     <Building className="h-3 w-3"/> E Wheels and Logistics
                 </span>
              </div>
            </Link>
            {/* Navigation buttons use ghost variant, inheriting text color */}
            <nav className="flex items-center gap-1 sm:gap-2"> {/* Reduced gap for more space */}
              {loggedInUser ? (
                <>
                   {isAdmin ? (
                    <>
                      <Button variant="ghost" size="sm" asChild className="text-xs sm:text-sm text-primary-foreground hover:bg-primary/80 hover:text-primary-foreground">
                          <Link href="/admin" className="flex items-center gap-1">
                          <UserCog className="h-4 w-4" />
                          <span>Attendance</span>
                          </Link>
                      </Button>
                      <Button variant="ghost" size="sm" asChild className="text-xs sm:text-sm text-primary-foreground hover:bg-primary/80 hover:text-primary-foreground">
                          <Link href="/admin/employees" className="flex items-center gap-1">
                          <Users className="h-4 w-4" />
                          <span>Employees</span>
                          </Link>
                      </Button>
                    </>
                   ) : (
                    <>
                    <Button variant="ghost" size="sm" asChild className="text-xs sm:text-sm text-primary-foreground hover:bg-primary/80 hover:text-primary-foreground">
                        <Link href="/" className="flex items-center gap-1">
                        <Home className="h-4 w-4" />
                        <span>Mark</span>
                        </Link>
                    </Button>
                    <Button variant="ghost" size="sm" asChild className="text-xs sm:text-sm text-primary-foreground hover:bg-primary/80 hover:text-primary-foreground">
                        <Link href="/history" className="flex items-center gap-1">
                        <History className="h-4 w-4" />
                        <span>History</span>
                        </Link>
                    </Button>
                    </>
                   )}
                  <Button variant="ghost" size="sm" onClick={handleLogout} className="text-xs sm:text-sm text-primary-foreground hover:bg-primary/80 hover:text-primary-foreground">
                     <LogOut className="h-4 w-4" />
                     <span className="hidden sm:inline">Logout</span> {/* Hide text on small screens */}
                  </Button>
                </>
              ) : (
                 <Button variant="ghost" size="sm" asChild className="text-xs sm:text-sm text-primary-foreground hover:bg-primary/80 hover:text-primary-foreground">
                    <Link href="/login" className="flex items-center gap-1">
                      <span>Login</span>
                    </Link>
                  </Button>
              )}
            </nav>
          </div>
        </header>
        )}
         {/* Main content area uses default background/foreground */}
        <main className={cn(
            "flex-1",
             // Add container padding unless it's the login page which has its own full-bleed background
            !isLoginPage && "container py-8 px-4 md:px-6"
        )}>
            {children}
        </main>
        <Toaster />
      </body>
    </html>
  );
}
